import React, { useEffect, useState } from "react"
import { Col, Row, Input } from "antd"
import { Section, Heading, Loading, Empty } from "components"
import { connect } from "react-redux"
import { fetchTuitionCategories, searchNearbyTuition, searchTuition, fetchNearbyPlacesGoogle } from "store/actions/placesActions"
import styled from "styled-components"
import { motion } from "framer-motion"
import { baseStyles } from "styles/base"
import PlaceCard from "../PlaceCard"
import { Link } from "react-router-dom"
import CategoryList from "components/common/CategoryList"
import { country } from "App"
import { Trans, t } from "@lingui/macro"
import { withI18n } from "@lingui/react"
import { useGeolocation } from "react-use"
import useInternational from "helpers/hooks/useInternational"

const FilterPanel = styled(motion.div)`
	margin-bottom: 1em;
	border-radius: 5px;
	padding: 1em 0;
	/* box-shadow: ${baseStyles.boxShadow.main}; */
	.right {
		text-align: right;
	}
`

const StyledInput = styled(Input.Search).attrs({
	// size: "large"
})`
	&& {
		margin-bottom: 1em;
		display: block;
		/* input[name="keyword"] {
			border-radius: 8px;
		} */
	}
`

const filterPanelVariants = {
	visible: { opacity: 1, y: 0, display: "block", transition: { staggerChildren: 0.2 } },
	hidden: { opacity: 0, y: -300, display: "none" },
}

function StudyCenter({ tuitionOptions, loading, searchNearbyTuition, i18n, tuitions, ...props }) {
	const [filterValues, setFilterValues] = useState({ category: 1, keyword: "", isSearch: false })
	const { latitude, longitude } = useGeolocation()
	const userDetails = JSON.parse(localStorage.getItem("userDetails")) || {}
	const isSG = userDetails.country === "sg"
	let { countryCode = "" } = useInternational()
	countryCode = countryCode.toLowerCase()
	// const userCountry = countryCode || country

	const { searchTuition } = props

	const renderTuitions = () => {
		if (loading)
			return (
				<Row>
					<Col lg={12}>
						<Loading />
					</Col>
				</Row>
			)

		if (tuitions.length === 0)
			return (
				<Section centered>
					<Empty description={<Trans>Oops! No data found for now</Trans>} />
				</Section>
			)

		return (
			<Row gutter={24}>
				{tuitions.map((item) => {
					const name = item.name.split(" ").join("-")
					return (
						<Col lg={6} xs={12} key={item.pvid} style={{ marginBottom: "2em" }}>
							<Link to={`/${country}/study_places/tuition/${name}--${item.pvid}`}>
								<PlaceCard css="cursor: pointer;" data={item} />
							</Link>
						</Col>
					)
				})}
			</Row>
		)
	}

	const handleSelectCategory = (category) => {
		setFilterValues({ ...filterValues, category })
		searchTuition(filterValues.keyword, category)
	}

	const handleSearchFilter = (value) => {
		setFilterValues({ ...filterValues, keyword: value, isSearch: true })
		searchTuition(value, countryCode, userDetails.pvid || 0)
	}

	useEffect(() => {
		if (latitude && longitude) {
			searchNearbyTuition({ lat: latitude, lng: longitude, radius: 10, country }, countryCode, userDetails.pvid || 0)
		}
	}, [countryCode, latitude, longitude, searchNearbyTuition, userDetails.pvid])

	return (
		<Section>
			<FilterPanel variants={filterPanelVariants} animate="visible" initial="hidden" transition={{ delay: 2 }}>
				<Row type="flex" justify="space-between">
					<Col lg={8} className="right">
						<StyledInput
							name="keyword"
							placeholder={i18n._(t`E.g. Study center in Bandung`)}
							onSearch={handleSearchFilter}
							allowClear
						/>
					</Col>
				</Row>
			</FilterPanel>
			<Heading
				bold
				level={4}
				marginBottom="1em"
				content={<Trans>Tuition center</Trans>}
				subheader={<Trans>List of tuition center</Trans>}
			/>
			{isSG && <CategoryList options={tuitionOptions} type={filterValues.category} onClick={handleSelectCategory} />}
			{renderTuitions()}
		</Section>
	)
}

const mapState = ({ place }) => {
	const tuitionOptions = (place.tuitionCategories || []).map((item) => ({ value: item.pvid, label: item.name }))

	return {
		tuitions: place.tuitions,
		loading: place.loading,
		tuitionCategories: place.tuitionCategories,
		tuitionOptions,
	}
}

export default connect(mapState, {
	fetchTuitionCategories,
	searchNearbyTuition,
	searchTuition,
	fetchNearbyPlacesGoogle,
})(withI18n()(StudyCenter))
